import React from "react";
import "./style.css";
import { Link } from "react-router-dom";

export default function About() {
  return (
    <div className="about">
      <h1>Você teve algum problema relacionado a algum serviço contratado?</h1>
      <h3>
        Caso você tenha passado por algum problema relacionado nos últimos anos,
        saiba que você tem direito a ser ressarcido pelos seus danos e
        prejuízos.
      </h3>
      <Link to="/verifique-seu-caso" className="verification">
        Avalie meu caso
      </Link>
    </div>
  );
}
