import React, { useState } from "react";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import "./style.css";
import Contact from "../../components/Contact";

export default function CaseAvaliation() {
  const services = ["Água", "Luz", "Negativação de nome", "Telecomunicações"];
  const [serviceSelected, setServiceSelected] = useState("");
  const [activeList, setActiveList] = useState("problem-case active");
  const [activeContact, setActiveContact] = useState("contact");
  function doubleFunctions() {
    setActiveList("problem-case");
    setActiveContact("contact active");
  }
  return (
    <div className="case-verification">
      <Header />
      <div onClick={doubleFunctions} className={activeList}>
        <h2>Selecione o problema enfrentado:</h2>
        <ul className="list-cases">
          {services.map((item) => (
            <li
              value={item}
              className="selected-service"
              onClick={(e) => setServiceSelected(item)}
              key={item}
            >
              {item}
            </li>
          ))}
        </ul>
      </div>
      <div>
        <Contact
          activeContact={activeContact}
          serviceSelected={serviceSelected}
        />
      </div>

      <Footer />
    </div>
  );
}
