import React, { useState } from "react";
import InputMask from "react-input-mask";
import { withRouter, useHistory } from "react-router";
import "./style.css";
import axios from "axios";

function Contact(props) {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [message, setMessage] = useState("");
  let history = useHistory();

  function handleClick(e) {
    e.preventDefault();
    if (e.target.className === "name") {
      setName(e.target.value);
    } else if (e.target.className === "email") {
      setEmail(e.target.value);
    } else if (e.target.className === "phone") {
      setPhone(e.target.value);
    } else if (e.target.className === "message") {
      setMessage(e.target.value);
    }
  }

  async function handleSubmit(e) {
    e.preventDefault();
    const form = {
      name,
      email,
      phone,
      subject: props.serviceSelected,
      message,
    };
    await axios.post("/api/sendMail", form);
    await history.push("/email-sucesso");
  }
  return (
    <div className={props.activeContact}>
      <form onSubmit={handleSubmit}>
        <input
          className="name"
          value={name}
          placeholder="Nome completo"
          onChange={handleClick}
          type="text"
        />
        <input
          className="email"
          value={email}
          placeholder="E-mail"
          onChange={handleClick}
          type="email"
        />
        <InputMask
          className="phone"
          value={phone}
          placeholder="Whatsapp"
          onChange={handleClick}
          type="tel"
          mask="(99) 99999-9999"
        />
        <input
          disabled
          className="subject"
          value={props.serviceSelected}
          placeholder="Assunto"
          onChange={handleClick}
          type="text"
        />
        <input
          className="message"
          value={message}
          placeholder="Mensagem"
          onChange={handleClick}
          type="text"
        />
        <button className="send-message" onClick={handleSubmit.bind(this)}>
          Enviar mensagem
        </button>
      </form>
    </div>
  );
}

export default withRouter(Contact);
